				"html": "<p>In JavaScript all variables and function names are case sensitive. This means that capitalization matters.</p>\n<p><code>MYVAR</code> is not the same as <code>MyVar</code> nor <code>myvar</code>. It is possible to have multiple distinct variables with the same name but different casing. It is strongly recommended that for the sake of clarity, you do not use this language feature.</p>\n<h2>Best Practice</h2>\n<p>Write variable names in Javascript in <code>camelCase</code>. In <code>camelCase</code>, <code>multi-word</code> variable names have the first word in lowercase and the first letter of each subsequent word is capitalized.</p>\n<h2>Examples:</h2>\n<pre><code>var someVariable;\nvar anotherVariableName;\nvar thisVariableNameIsTooLong;\n</code></pre>",
